<template>
  <div class="business-portal-page">
    <!-- title -->
    <portal-header :projectObj="titleConfig"></portal-header>
    <!-- body -->
    <portal-form
      ref="searchList"
      :form-data="searchList"
      :form-rules="formRules"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    >
      <!-- 新需求 -->
      <template v-slot:oppositeParty>
        <el-row class="grouped-input" :gutter="4">
          <el-col class="grouped-input-type" :span="handleSpanWidth">
            <el-form-item :label="$t('IDC.accountStatement.oppositeParty')" prop="oppositePartyType">
              <el-select
                v-model="searchList.oppositePartyType"
                :placeholder="$t('common.placeholder.select')"
                @change="handleSelect"
              >
                <el-option v-for="item in partySelectOptions" :label="item.label" :key="item.value" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col class="grouped-input-value" :span="16" v-if="showRightCode">
            <el-form-item
              class="hidden-label"
              :label="oppositeInputTitleLabel"
              prop="oppositeParty"
              :required="oppositePartyRequired"
            >
              <el-input
                :placeholder="oppositeInputTitleLabel"
                v-model="searchList.oppositeParty"
                class="input-with-select"
                @blur="debitPartyShortCodeBlur($event)"
                readonly="readonly"
              >
                <i
                  v-if="searchList.oppositePartyType !== 'all' || searchList.oppositePartyType !== '8000'"
                  class="el-icon-more icon-cursor"
                  slot="suffix"
                  @click="openDebitShortCodeDialog"
                >
                </i>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </template>
    </portal-form>
    <entity-search-dialog
      :dialog-title="entityDialogTitle"
      :dialogVisible.sync="dialogVisible"
      :fieldName="inputField"
      @select="receiverSelected"
      @onCloseDialog="closeCallBack"
    >
    </entity-search-dialog>
    <div class="table-list-container">
      <el-dropdown @command="handleExportClick">
        <el-button :disabled="exportDisabled" type="primary" size="small" class="margin-bottom-16">
          {{ $t('common.button.export') }}
          <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="Excel">Excel</el-dropdown-item>
          <el-dropdown-item command="Excel2007">Excel 2007</el-dropdown-item>
          <el-dropdown-item command="CSV">CSV</el-dropdown-item>
          <el-dropdown-item command="PDF">PDF</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <table-page
        size="small"
        ref="data-table"
        class="table-page"
        :has-pagination="hasPagination"
        :columns="columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="refreshTableFlag"
      >
        <template slot="orderIdOperation" slot-scope="scope">
          <el-button type="text" @click="showDetail(scope.data.row)" style="padding: 0">
            {{ scope.data.row.tradeOrderId }}
          </el-button>
        </template>
        <template slot="operation" slot-scope="scope">
          <el-button
            v-if="isShowReverse(scope.data.row)"
            type="text"
            class="btn"
            @click="reverse(scope.data.row)"
            style="padding: 0"
          >
            {{ $t('IDC.reviewTransaction.button.reverse') }}
          </el-button>
          <el-button
            v-if="isShowProcess(scope.data.row)"
            type="text"
            class="btn"
            @click="goToProcessPage(scope.data.row)"
            style="padding: 0"
          >
            {{ $t('common.button.process') }}
          </el-button>
        </template>
        <template slot="transactionStatus" slot-scope="scope">
          <span class="status-circle" :style="statusStyle(scope.data.row.transactionStatus)"></span>
          <span>{{ scope.data.row.transactionStatus }}</span>
        </template>
      </table-page>
    </div>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import { selectRequiredValidateObj, nonSpecialCharValidateObj } from '@/common/validator/common';
import entitySearchDialog from '@/views/identifier-center/pages/account-statement/components/entity-search-dialog.vue';

import {
  queryTransactionList,
  queryAccountList,
  exportOrderList,
} from '@/views/identifier-center/api/api-transaction.js';
import { dateFormat } from '@/common/utils/date-time-utils';

import dayjs from 'dayjs';

const Identity_type = {
  all: 'all',
  customer: '1000',
  group: '7000',
  organization: '5000',
  sp: '8000',
};

export default {
  name: 'ReviewTrasaction',
  components: { PortalHeader, TablePage, PortalForm, entitySearchDialog },
  props: {
    activeName: {
      type: String,
      default: '',
    },
    identityId: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      dialogVisible: false,
      entityDialogTitle: '',
      inputField: '',
      handleSpanWidth: 24,
      showRightCode: false,
      partySelectOptions: [
        {
          label: 'All',
          value: Identity_type.all,
        },
        {
          label: 'Organization',
          value: Identity_type.organization,
        },
        {
          label: 'Customer',
          value: Identity_type.customer,
        },
        {
          label: 'Group',
          value: Identity_type.group,
        },
        {
          label: 'SP',
          value: Identity_type.sp,
        },
      ],
      oppositeInputTitleLabel: '',
      oppositePartyRequired: true,
      isSearchTableData: false,
      identityType: '',
      searchLoading: false,
      searchVisible: false,
      refreshTableFlag: false,
      hasPagination: true,
      exportDisabled: true,
      totalRecords: 0,
      relatedAccountList: [],
      searchList: {
        queryMode: '1',
        initiationTime: this.getInitTime(),
        transactionStatus: 'all',
        debit: '',
        relatedAccount: '',
        oppositeParty: '',
        oppositePartyId: '',
        oppositePartyType: 'all',
        receiptNo: '',
      },
      columns: [
        {
          label: this.$t('IDC.reviewTransaction.label.receiptNo'),
          prop: 'tradeOrderId',
          align: 'left',
          __slotName: 'orderIdOperation',
          'min-width': 150,
        },
        {
          label: this.$t('IDC.reviewTransaction.label.completionTime'),
          prop: 'finishTime',
          align: 'left',
          'min-width': 180,
        },
        {
          label: this.$t('IDC.reviewTransaction.label.details'),
          prop: 'details',
          align: 'left',
          'min-width': 185,
        },
        {
          label: this.$t('IDC.reviewTransaction.label.oppositeParty'),
          prop: 'opposityPartyName',
          align: 'left',
          'min-width': 185,
        },
        {
          label: this.$t('IDC.reviewTransaction.label.transactionStatus'),
          prop: 'transactionStatus',
          align: 'left',
          'min-width': 150,
          __slotName: 'transactionStatus',
        },
        {
          label: this.$t('IDC.reviewTransaction.label.currency'),
          prop: 'transactionCurrency',
          align: 'left',
          'min-width': 120,
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          fixed: 'right',
          __slotName: 'operation',
          'min-width': 93,
        },
      ],
      modeOptions: [
        { label: 'Transactions as Debit/Credit Party', value: '1' },
        { label: 'Transactions as Receipt No.', value: '2' },
      ],
      statusOptions: [
        {
          label: 'All',
          value: 'all',
        },
        {
          label: 'Authorized',
          value: 'Authorized',
        },
        {
          label: 'Pending Authorized',
          value: 'Pending Authorized',
        },
        {
          label: 'Refund Pending Authorized',
          value: 'Refund Pending Authorized',
        },
        {
          label: 'Completed',
          value: 'Completed',
        },
        {
          label: 'Cancelled',
          value: 'Cancelled',
        },
        {
          label: 'Declined',
          value: 'Declined',
        },
        {
          label: 'Expired',
          value: 'Expired',
        },
        {
          label: 'Refunded',
          value: 'Refunded',
        },
      ],

      debitOptions: [
        { label: 'Both', value: '' },
        { label: 'Debit', value: '1' },
        { label: 'Credit', value: '0' },
      ],
      relatedAccountOptions: [{ label: 'All', value: '' }],
      oppositePartyOptions: [
        {
          label: 'All',
          value: '',
        },
        {
          label: 'Customer',
          value: '10',
        },
        {
          label: 'Group',
          value: '30',
        },
        {
          label: 'Organization',
          value: '50',
        },
        {
          label: 'SP',
          value: '00',
        },
      ],
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      formRules: {
        queryMode: [selectRequiredValidateObj],
        initiationTime: [selectRequiredValidateObj],
        transactionStatus: [selectRequiredValidateObj],
        receiptNo: [nonSpecialCharValidateObj],
      },
      pickerOptions: {
        disabledDate: (time) => {
          let startTime = dayjs().startOf('date').subtract(1, 'month');
          let endTime = dayjs().endOf('date');
          return time.getTime() < startTime || time.getTime() > endTime;
        },
      },
    };
  },
  computed: {
    titleConfig() {
      return {
        title: this.$t('IDC.reviewTransaction.pageTitle'),
        desc: this.$t('IDC.reviewTransaction.pageDesc'),
      };
    },
    formConfigList() {
      if (this.searchList.queryMode === '2') {
        return this.receiptNoConfigList;
      } else {
        return this.debitPartyConfigList;
      }
    },
    receiptNoConfigList() {
      return [
        {
          label: this.$t('IDC.reviewTransaction.label.queryMode'),
          key: 'queryMode',
          comp: 'el-select',
          options: this.modeOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'queryMode',
          },
        },
        {
          label: this.$t('IDC.reviewTransaction.label.receiptNo'),
          key: 'receiptNo',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'receiptNo',
          },
        },
      ];
    },
    debitPartyConfigList() {
      return [
        {
          label: this.$t('IDC.reviewTransaction.label.queryMode'),
          key: 'queryMode',
          comp: 'el-select',
          options: this.modeOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'queryMode',
          },
        },
        {
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          align: 'left',
          label: this.$t('IDC.reviewTransaction.label.initiationTime'),
          description: this.$t('IDC.reviewTransaction.label.initiationTimeDesc'),
          prop: 'initiationTime',
          comp: [
            {
              key: 'initiationTime',
              comp: 'el-date-picker',
              bind: {
                id: 'initiationTime',
                type: 'datetimerange',
                format: 'dd/MM/yyyy HH:mm:ss',
                'range-separator': '~',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': 'Please select',
                'end-placeholder': 'Please select',
                'picker-options': this.pickerOptions,
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
        {
          label: this.$t('IDC.reviewTransaction.label.transactionStatus'),
          key: 'transactionStatus',
          comp: 'el-select',
          options: this.statusOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'transactionStatus',
          },
        },
        {
          label: this.$t('IDC.reviewTransaction.label.debit'),
          key: 'debit',
          comp: 'el-select',
          options: this.debitOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'debit',
          },
        },
        {
          label: this.$t('IDC.reviewTransaction.label.relatedAccount'),
          key: 'relatedAccount',
          comp: 'el-select',
          options: this.relatedAccountOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'relatedAccount',
          },
        },
        {
          slotName: 'oppositeParty',
        },
      ];
    },
    statusStyle() {
      return (status) => {
        switch (status) {
          case 'Initiated':
            return { 'background-color': `${this.GlobalStyleVariables.initialColor}` };
          case 'Completed':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case 'Pending':
          case 'Authorized':
          case 'Pending_Authorized':
          case 'Refund':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorSAecondaryWarning}` };
          case 'cancelled':
          case 'Declined':
          case 'Expired':
            return { 'background-color': `${this.GlobalStyleVariables.statusDark}` };
          default:
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
        }
      };
    },
  },
  watch: {
    'searchList.queryMode': {
      handler() {
        this.$refs['searchList'].clearValidate();
      },
      deep: true,
    },
    'searchList.oppositePartyType': {
      handler(value) {
        this.searchList.oppositeParty = '';
        if (value === Identity_type.all) {
          this.showRightCode = false;
          this.handleSpanWidth = 24;
        }
        if (value === Identity_type.organization) {
          this.oppositeInputTitleLabel = this.$t('TRC.reviewTransaction.label.shortCode');
          this.handleSpanWidth = 8;
          this.showRightCode = true;
        } else if (value === Identity_type.customer) {
          this.oppositeInputTitleLabel = this.$t('TRC.reviewTransaction.label.MSISDN');
          this.handleSpanWidth = 8;
          this.showRightCode = true;
        } else if (value === Identity_type.group) {
          this.oppositeInputTitleLabel = this.$t('TRC.reviewTransaction.label.groupCode');
          this.handleSpanWidth = 8;
          this.showRightCode = true;
        } else if (value === Identity_type.sp) {
          this.handleSpanWidth = 24;
          this.showRightCode = false;
        }
      },
      immediate: true,
      deep: true,
    },
  },
  created() {
    if (this.activeName === 'orgOperator') {
      this.identityType = '3000';
    } else {
      this.identityType = '5000';
    }
  },
  activated() {},
  mounted() {
    this.getRelatedAccountOptions();
  },
  methods: {
    openDebitShortCodeDialog() {
      if (
        this.searchList.oppositePartyType === '1000' ||
        this.searchList.oppositePartyType === '5000' ||
        this.searchList.oppositePartyType === '7000'
      ) {
        this.dialogVisible = true;
      } else {
        this.dialogVisible = false;
      }
    },
    // 新需求 获取实体信息相关代码 oppositePartyId
    receiverSelected(val) {
      const objMap = {
        msisdn: 'identityId',
        shortCode: 'organizationId',
        groupCode: 'identityId',
      };
      this.searchList.oppositeParty = val[this.inputField];
      this.searchList.oppositePartyId = val[objMap[this.inputField]];
    },
    closeCallBack(val) {
      this.dialogVisible = val;
    },

    // 弹窗标题
    handleSelect(entityCode) {
      this.searchList.oppositeParty = '';
      this.searchList.oppositePartyId = '';
      switch (entityCode) {
        case '1000':
          this.entityDialogTitle = 'Select Customer';
          this.inputField = 'msisdn';
          break;
        case '5000':
          this.entityDialogTitle = 'Select Organization'; // Short Code
          this.inputField = 'shortCode';
          break;
        case '7000':
          this.entityDialogTitle = 'Select Group Code';
          this.inputField = 'groupCode';
          break;
      }
    },
    debitPartyShortCodeBlur(event) {
      if (!event.target.value) {
        return;
      }
    },
    getInitTime() {
      let startTime = new Date(dayjs().startOf('date').subtract(1, 'month'));
      let endTime = new Date(dayjs().endOf('date'));
      return [startTime, endTime];
    },

    isShowReverse(row) {
      if (!row.reverseMode) {
        return false;
      }
      return (row.transactionStatus === 'Paid' || row.transactionStatus === 'Completed') && row.reverseMode !== 'A';
    },
    isShowProcess(row) {
      return row.transactionStatus === 'Authorized' || row.transactionStatus === 'Pending Authorized';
    },

    async getRelatedAccountOptions() {
      let params = { identityId: this.identityId };
      let result = await queryAccountList(params);
      if (result.list && result.list.length > 0) {
        this.relatedAccountList = result.list;
        result.list.forEach((item) => {
          this.relatedAccountOptions.push({
            label: item.accountName,
            value: item.accountNo,
          });
        });
      }
    },

    reset() {
      this.clearSearchListData();
      this.search();
    },
    clearSearchListData() {
      this.searchList.initiationTime = this.getInitTime();
      this.searchList.transactionStatus = 'all';
      this.searchList.debit = '';
      this.searchList.relatedAccount = '';
      this.searchList.oppositeParty = '';
      this.searchList.receiptNo = '';
    },
    search() {
      this.searchLoading = true;
      this.$refs['searchList'].innerFormRef.validate((valid) => {
        if (!valid) {
          this.searchLoading = false;
          return;
        }
        this.paginationConfig = {
          total: 0,
          pageIndex: 1,
          pageSize: 10,
        };
        this.refreshTableFlag = true;
      });
    },
    showDetail(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.identityId,
        identityType: this.identityType,
        status: row.transactionStatus,
        type: 'detail',
        from: 'transaction',
      };
      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },

    reverse(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.identityId,
        identityType: this.identityType,
        type: 'reverse',
        from: 'transaction',
      };
      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },

    goToProcessPage(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.identityId,
        identityType: this.identityType,
        type: 'process',
        from: 'transaction',
      };
      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },

    getTableData() {
      let params = {
        identityType: this.identityType,
        tradePartyId: this.identityId,
        createTimeBegin: this.searchList.initiationTime
          ? dayjs(this.searchList.initiationTime[0]).format('YYYY-MM-DD HH:mm:ss')
          : '',
        createTimeEnd: this.searchList.initiationTime
          ? dayjs(this.searchList.initiationTime[1]).format('YYYY-MM-DD HH:mm:ss')
          : '',
        accountNo: this.searchList.relatedAccount,
        oppositePartyId: this.searchList.oppositePartyId,
        oppositePartyType: this.searchList.oppositePartyType === '8000' ? this.searchList.oppositePartyType : '',
        transactionRole: this.searchList.debit,
        status: this.searchList.transactionStatus === 'all' ? [] : [this.searchList.transactionStatus],
        startNumber: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
      };
      if (this.searchList.queryMode === '2') {
        params = {
          identityType: this.identityType,
          tradePartyId: this.identityId,
          startNumber: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
          tradeOrderId: this.searchList.receiptNo,
        };
      }
      this.searchLoading = true;
      return new Promise((resolve) => {
        queryTransactionList(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.totalSize || 0;
              res.list = res.spOrderQueryDTOList || [];
              res.list = res.list.map((order) => {
                if (order.finishTime) {
                  order.finishTime = dayjs(order.finishTime).format('DD/MM/YYYY HH:mm:ss');
                }
                return order;
              });
            } else {
              res.totalRecords = 0;
              res.list = [];
            }
            this.exportDisabled = res.list.length > 0 ? false : true;
            resolve(res);
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },

    handleExportClick(fileType) {
      this.$refs['searchList'].validate((valid) => {
        if (valid) {
          let selectedAccount = this.relatedAccountList.filter((account) => {
            return account.accountNo === this.searchList.relatedAccount;
          });
          let params = {
            tradePartyId: this.identityId,
            identityType: this.identityType,
            accountType: selectedAccount.length ? selectedAccount[0].accountTypeId : '',
            accountNo: selectedAccount.length ? selectedAccount[0].accountNo : '',
            oppositePartyId: this.searchList.oppositePartyId,
            oppositePartyType: this.searchList.oppositePartyType === '8000' ? this.searchList.oppositePartyType : '',
            transactionRole: this.searchList.debit === 'both' ? '' : this.searchList.debit,
            initiatorIdentityId: '',
            serviceCodes: [],
            status: this.searchList.transactionStatus == 'all' ? [] : [this.searchList.transactionStatus],
            createTimeBegin: this.searchList.initiationTime
              ? dayjs(this.searchList.initiationTime[0]).format('YYYY-MM-DD HH:mm:ss')
              : '',
            createTimeEnd: this.searchList.initiationTime
              ? dayjs(this.searchList.initiationTime[1]).format('YYYY-MM-DD HH:mm:ss')
              : '',
            exportType: fileType,
            requestType: 'allTran',
            reasonType: '',
            currency: '',
          };
          if (this.searchList.queryMode === '2') {
            params = {
              identityType: this.identityType,
              tradePartyId: this.identityId,
              tradeOrderId: this.searchList.receiptNo,
            };
          }
          exportOrderList(params)
            .then((res) => {
              if (fileType === 'Excel2007') {
                fileType = 'XLS';
              } else if (fileType === 'Excel') {
                fileType = 'XLSX';
              }
              if (res.type === 'application/json') {
                const reader = new FileReader();
                reader.readAsText(res);
                const _this = this;
                reader.onload = function () {
                  const errorData = JSON.parse(reader.result);
                  _this.$modal.msgError(errorData.header.responseDesc);
                };
              } else {
                const start = new Date();
                const date = dateFormat(start, 'YYYYMMDDHHmmss');
                const blob = new Blob([res], { type: 'application/ms-excel' }); // 解析后端返回的乱码
                const elink = document.createElement('a');
                elink.download = 'TRANSACTION_' + date + '.' + fileType.toLowerCase(); // 定义下载名字
                elink.style.display = 'none'; // 决定是否隐藏
                elink.href = URL.createObjectURL(blob);
                document.body.appendChild(elink);
                elink.click();
                URL.revokeObjectURL(elink.href); // 释放URL 对象
                document.body.removeChild(elink);
              }
            })
            .catch()
            .finally();
        }
      });
    },
  },
};
</script>

<style scoped lang="scss">
/deep/ .el-table__fixed-right::before,
.el-table__fixed::before {
  height: 0;
  background: transparent;
}
.grouped-input {
  .grouped-input-type {
    /deep/.el-form-item {
      &.hidden-label {
        .el-form-item__label {
          visibility: hidden;
        }
      }
      .el-form-item__label {
        overflow: visible;
        white-space: nowrap;
      }
    }
  }

  .grouped-input-value {
